import "../CSS/Github.css"
import React, { Component } from 'react'
import GithubCalendar from "react-github-calendar";

export class Github extends Component {
  static propTypes = {}

  render() {
    return (
      <div className="git_hub">
        <div className="gittitle" data-aos="zoom-out" data-aos-delay="50" data-aos-duration="1000">
          <h1 style={{ display: "inline-Block", color: "purple", }}><p style={{ color: "white", display: "inline-Block" }}>My</p>Github</h1>
        </div>

        <div className="react-activity-calendar" align="center">
          <GithubCalendar
            username="VaishnaviR99"
            blockMargin={4}
            blockSize={18}
            blockRadius={2}
           
          />

        </div>
        <div align="center"  className="github-streak-stats">
          <a href="https://git.io/streak-stats"><img src="https://streak-stats.demolab.com?user=VaishnaviR99" /></a>
        </div>
        <div align="center" className="github-top-langs" >
          <a href="https://github-readme-stats"><img src="https://github-readme-stats-git-masterrstaa-rickstaa.vercel.app/api/top-langs/?username=VaishnaviR99" /></a>
        </div>

        
      </div>

    )
  }
}

export default Github