import React from "react";
import me from "../media/me.jpeg"
import { useState, useEffect } from "react"
import "../CSS/Home.css";
import linkedin from "../media/linkedin.png";
import github from "../media/github.png";
import mail from "../media/mail.png";
import call from "../media/call.png";
import unicornT from "../media/unicornT.png";
import AOS from 'aos';
import 'aos/dist/aos.css';
AOS.init();



const Home = () => {



  const [sentences, setSentences] = useState([
    "MERN Stack Developer !",
    "Full-Stack Developer !",
    "Front-End Developer !",
    "Back-End Developer !"
  ]);
  const [index, setIndex] = useState(0);
  const [typedText, setTypedText] = useState('');

  useEffect(() => {
    const intervalId = setInterval(() => {
      if (typedText === '') {
        // start typing new sentence
        const sentence = sentences[index];
        for (let i = 0; i <= sentence.length; i++) {
          setTimeout(() => {
            setTypedText(sentence.slice(0, i));
          }, i * 50); // Reduce the delay between each typed letter
        }
      } else {
        // start erasing current sentence
        for (let i = typedText.length; i >= 0; i--) {
          setTimeout(() => {
            setTypedText(typedText.slice(0, i));
          }, (typedText.length - i) * 40); // Reduce the delay between each deleted letter
        }
      }

      // switch to next sentence
      setTimeout(() => {
        setIndex((index + 1) % sentences.length);
      }, sentences[index].length * 50 + 1000); // Reduce the delay between sentence changes

    }, sentences[index].length * 50 + 2000); // Reduce the overall delay

    return () => clearInterval(intervalId);
  }, [sentences, index, typedText]);

  const openLink = (link) => {
    window.open(link, "_blank");
  };

  const recipient = "ralegaonkarvaishnavi@gmail.com";
  const phoneNumber = "8788824031";




  return (
    <>
      <div className="nav-link home" >
        {/* <div class="rainbow"></div>
        <img className="unicorn" src={unicornT} alt="unicorn" /> */}
        <div className="topdiv" >
          <div className="intro_text">

            <img className="home-img" src={me} alt="me" />
            <h3
              data-aos="fade-right"
              data-aos-delay="400"
              data-aos-duration="1000"
            >Hello there, I'm</h3>
            <h1
              className="user-detail-name"
              data-aos="fade-right"
              data-aos-delay="500"
              data-aos-duration="1000"
            >Vaishnavi Ralegaonkar</h1>
            <h2
              data-aos="fade-right"
              data-aos-delay="600"
              data-aos-duration="1000"
            > a {typedText}
              <span className="cursor" />|
            </h2>
            <span id="type" data-aos="fade-right" class="wrap" className="txt-rotate" data-period="2000" data-rotate='["MERN Stack Developer", "Full-Stack Developer", "Front-End Developer","Back-End Developer",]'  ></span>

          </div>
        </div>

        <div className="contact_box">
          <img
            src={linkedin}
            alt="linedin"
            className="linkedin"
            data-aos="zoom-in"
             data-aos-offset="50"
            data-aos-delay="300"
            data-aos-duration="1000"
            onClick={() => openLink("https://www.linkedin.com/in/vaishnavir31/")}
          />
          <img
            src={github}
            alt="github"
            className="linkedin"
            data-aos="zoom-in"
            data-aos-offset="50"
            data-aos-delay="300"
            data-aos-duration="1000"
            onClick={() => openLink("https://github.com/VaishnaviR99")}
          />
          <img
            src={mail}
            alt="mail"
            className="linkedin"
            data-aos="zoom-in"
            data-aos-offset="50"
            data-aos-delay="300"
            data-aos-duration="1000"
            onClick={() => window.location.href = `mailto:${recipient}`}
          />
          <img
            src={call}
            alt="call"
            className="linkedin"
            data-aos="zoom-in"
            data-aos-offset="50"
            data-aos-delay="300"
            data-aos-duration="1000"
            onClick={() => window.location.href = `tel:${phoneNumber}`}
          />
        </div>

      </div>










    </>
  )
}

export default Home;