import "../CSS/About.css";

// import coding from "../media/coding.gif";
// import coding1 from "../media/coding1.gif";
import coding2 from "../media/coding2.gif";
import bullet from "../media/ut.png";



const About = () => {
    return (
        <>

            <div className="nav-link about">
                <div className="heading" data-aos="zoom-out" data-aos-delay="50" data-aos-duration="1000">
                    <h1 style={{ display: "inline-Block", }}><p style={{ color: "white", display: "inline-Block" }}>About</p>Me</h1>
                </div >


                <div className="details">
                    <img
                        src={coding2}
                        alt="code"
                        className="code"
                        data-aos="fade-right"
                        data-aos-delay="50"
                        data-aos-duration="1000"
                    />
                    <div
                        className="aboutme"
                        data-aos="fade-left"
                        data-aos-delay="50"
                        data-aos-duration="1000"
                    >
                        <ul className="user-detail-intro" >
                            <li style={{ marginBottom: "20px" }}><span className="bullet"><  img src={bullet} alt="uni" /></span>Hi, I'm Vaishnavi Ralegaonkar an aspiring and determined Full Stack Web Developer, curious to explore different industry-standard tech stacks and environments. </li>

                            <li style={{ marginBottom: "20px" }}><span className="bullet"><  img src={bullet} alt="uni" /></span>I was previously unable to pursue my passion for coding due some constraint ,
                                but Masai School provided me with the opportunity to continue and hone my skills. </li>
                            <li style={{ marginBottom: "20px" }}><span className="bullet"><  img src={bullet} alt="uni" /></span>Through their program,
                                I have gained proficiency in essential web development technologies such as HTML, CSS, JavaScript, and React. These skills have equipped me to develop dynamic and engaging web applications and interfaces,
                                and I am excited to continue honing my abilities in this field.</li>
                            <li style={{ marginBottom: "20px" }}><span className="bullet"><  img src={bullet} alt="uni" /></span>I'm excited to collaborate with your team to bring your vision to life!</li>

                        </ul>


                    </div>

                </div>
            </div>
        </>

    )
}

export default About;