
import airbnbi from "../media/airbnb.png";
import hotstari from "../media/hotstar.png";


import "../CSS/Projects.css";
import github from "../media/github.png";
import deploy from "../media/deploy.png";


const projects = [
  {
    "img1": airbnbi,
    "name": "Airbnb-Clone",
    "about": "Airbnb is a popular online marketplace and hospitality service that allows people to rent out their homes,apartments, or other types of lodging to travelers.It  provides a more authentic and personalized experience for guests, as they have the opportunity to stay in unique and local accommodations and interact with their hosts.",
    "collaborator": "5 Collaborators",
    "duration": "06 Days",
    "tool": "HTML | CSS |Javascript",
    "githubLink": "https://github.com/Prathameshschavan/Project_Airbnb.com",
    "deployedLink": "https://dashing-lokum-307e4a.netlify.app/html/product.html",
    "airbnb": true,
    "hotstart": false,
  },
  {
    "img1": hotstari,
    "name": "Disney plus Hotstar -Clone",
    "about": "Disney+ Hotstar is a popular streaming service that offers a wide variety of content, including TV shows, movies, and live sports.Disney+ Hotstar has different subscription plans, including a free plan with limited content, a premium plan with access to all content, and a VIP plan that offers access to local content and live sports.",
    "collaborator": "5 Collaborators",
    "duration": "06 Days",
    "tool": "React | CSS | Chakra-UI ",
    "githubLink": "https://github.com/ritikraj07/Project_Disney-Hotstar.com",
    "deployedLink": "https://hotstarclone07.netlify.app/",
    "airbnb": false,
    "hotstart": true,
  }

]



const Projects = () => {
  console.log(projects)


  return (
    <>
      <div className='nav-link projects'>

        <div className="heading" data-aos="zoom-out" data-aos-delay="50" data-aos-duration="1000">
          <h1 style={{ display: "inline-Block", }}><p style={{ color: "white", display: "inline-Block" }}>My</p>Projects</h1>
        </div >

        <div className='project_cards'>
          {projects.map((project) => (

            <div className="project-card" key={projects.name}>

              <div className='displayimg' ><img 
              className='displayimg' 
              data-aos="flip-left" 
              data-aos-duration="1300"
              src={(project.airbnb) ? airbnbi : hotstari}
               alt={project.name} 
               />
               </div>

              <div>
                <h2 className='project-title'>{project.name}</h2>
                <p className='project-description'>{project.about}</p>
                <p className='group'><b>Group Project :</b> {project.collaborator} | <b>Duration :</b>{project.duration}</p>
              </div>
              <div className='project-tech-stack'> <h3 style={{ color: "purple" }}>Tech-Stack </h3><h3>{project.tool}</h3></div>
              <div className='view_div'>
                
                  <img className='project-github-link' src={github} alt="github" onClick={( ) => window.open(project.githubLink, "_blank")} />
                
                
                  <img className='project-deployed-link' src={deploy} alt="deploy"  onClick={( ) => window.open(project.deployedLink, "_blank")}/>
               
              </div>

            </div>
          ))}

        </div>
      </div>
    </>
  );
};


export default Projects;