import "../CSS/Contact.css"
import call from "../media/call.png";
const Contacts = () => {

    const openLink = (link) => {
        window.open(link, "_blank");
      };
    
      const recipient = "ralegaonkarvaishnavi@gmail.com";
      const phoneNumber = "8788824031";
    
    return (
        <div className="nav-link contact">
            <div className="contact" data-aos="zoom-out" data-aos-delay="50" data-aos-duration="1000">
                <h1 style={{ display: "inline-Block", color: "purple", }}><p style={{ color: "white", display: "inline-Block" }}>Contact</p>Me</h1>
            </div>
            
            {/* <h1 className="namec">Vaishnavi Ralegaonkar</h1> */}
            <h2 className="reach" data-aos="fade-right" data-aos-delay="100" data-aos-duration="1000"><span style={{color:"purple"}}>Reach Out</span> to me!</h2>
            <div className="mediaa">
                <h3 className="contact-linkedin" data-aos="flip-up" data-aos-delay="100" data-aos-duration="2000" onClick={() => openLink("https://www.linkedin.com/in/vaishnavir31/")}>LinkedIn</h3>
                <h3 className="contact-github" data-aos="flip-up" data-aos-delay="100" data-aos-duration="2000" onClick={() => openLink("https://github.com/VaishnaviR99")} >Github</h3>
                <h3 className="contact-email" data-aos="flip-up" data-aos-delay="100" data-aos-duration="2000" onClick={() => window.location.href = `mailto:${recipient}`}>Email</h3>
                <img src={call} data-aos="flip-up" data-aos-delay="100" data-aos-duration="2000" alt="call" className="contact-phone" onClick={() => window.location.href = `tel:${phoneNumber}`}/>

            </div>
            <p className="tag">Designed and build with <span style={{color:"red", fontSize:"20px"}}>&#10084;</span>  by Vaishnavi </p>
        </div>
    )
}

export default Contacts;