import "../CSS/NAVBAR.css";
import mypdf from "../media/pdf.pdf";
import white_logo1 from "../media/white_logo1.png";
import { Link, animateScroll } from "react-scroll";

const Topnav = () => {
  const handleDownload = () => {
    const link = document.createElement("a");
    link.href = mypdf;
    link.download = mypdf;
    link.click();
    const newWindow = window.open(mypdf, "_blank", "noopener,noreferrer");
    if (newWindow) {
      newWindow.opener = null;
    }
  };

  const scrollToTop = (id) => {
    animateScroll.scrollTo(id, {
      duration: 500,
      delay: 0,
      smooth: "easeInOutQuart",
    });
  };

  return (
    <div className="nav-menu" id="nav-menu">
      <div className="toggle">
        <span
          className="drpdns"
          class="material-symbols-outlined"
          style={{ marginLeft: "30px", fontSize: "30px", cursor: "pointer" }}
        >
          menu
        </span>
        <div className="dropdown_cont">
          <h3 className="contents">HOME</h3>
          <h3 className="contents">ABOUT</h3>
          <h3 className="contents">SKILLS</h3>
          <h3 className="contents">PROJECTS</h3>
          <h3 className="contents">CONTACT</h3>
        </div>
      </div>

      <div className="my_logo" align="left">
        <img align="left" src={white_logo1} alt="logo" />
      </div>

      <div
        className="box"
        data-aos="fade-down"
        data-aos-delay="100"
        data-aos-duration="1000"
      >
        <Link
          className="contents"
          id="home"
          activeClass="active"
          to="nav-link home"
          spy={true}
          smooth={true}
          offset={-70}
          duration={500}
          onClick={() => scrollToTop("home")}
        >
          HOME
        </Link>
        <Link
          className="contents"
          id="about"
          activeClass="active"
          to="nav-link about"
          spy={true}
          smooth={true}
          offset={-70}
          duration={500}
          onClick={() => scrollToTop("about")}
        >
          ABOUT
        </Link>
        <Link
          className="contents"
          id="skills"
          activeClass="active"
          to="nav-link skills"
          spy={true}
          smooth={true}
          offset={-70}
          duration={500}
          onClick={() => scrollToTop("skills")}
        >
          SKILLS
        </Link>
        <Link
          className="contents"
          id="nav-link projects"
          activeClass="active"
          to="projects"
          spy={true}
          smooth={true}
          offset={-70}
          duration={500}
          onClick={() => scrollToTop("projects")}
        >
          PROJECTS
        </Link>
        <Link
          className="contents"
          id="contact"
          activeClass="active"
          to="nav-link contact"
          spy={true}
          smooth={true}
          offset={-70}
          duration={500}
          onClick={() => scrollToTop("contact")}
        >
          CONTACT
        </Link>
      </div>
      <button className="nav-link resume" onClick={handleDownload}>
        <span class="material-symbols-outlined">download</span>
        RESUME
      </button>
    </div>
  );
};

export default Topnav;
